*{
margin: 0;
padding: 0;

}
.img {
  width: 400px;
  height: 400px;
}
.main-div h1 {
  margin-left: 540px;
  margin-top: -10px;
}

.menu-div  {
  width: 100%;
  height: 80px;
  background-color: #3e8e41;
  color: aliceblue;
  
}
.menu-div ul {
  height: 80px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.menu-div ul li {
  list-style: none;
  text-transform: uppercase;
}
.menu-div ul li p {
  cursor: pointer;
}
.btn {
  display: inline-block;
  /* justify-content: space-around; */
}
.modal {
  width: 400px;
  height: 505px;
  margin-left: 350px;
  margin-top: 60px;
  border-radius: 10px;
}
.close-btn {
  margin-left: 140px;
  margin-top: 10px;
  font-size: 16px;
  height: 30px;
  width: 100px;
  border-radius: 10px;
  background-color: brown;
  color: aliceblue;
}
